	module.exports = {"data":{"site":{"siteMetadata":{"title":"Gatsby Starter Blog","author":"Kyle Mathews"}},"markdownRemark":{"id":"/Users/jeff/Documents/coding/gatsbyBlog/src/pages/testPost/index.md absPath of file >>> MarkdownRemark","html":"<p>This is my first post on my new fake blog! How exciting!</p>\n<p><a href=\"http://en.wikipedia.org/wiki/Salted_duck_egg\">salted duck eggs</a>.</p>\n<blockquote>\n<p>A salted duck egg is a Chinese preserved food product made by soaking duck\neggs in brine, or packing each egg in damp, salted charcoal. In Asian\nsupermarkets, these eggs are sometimes sold covered in a thick layer of salted\ncharcoal paste. The eggs may also be sold with the salted paste removed,\nwrapped in plastic, and vacuum packed. From the salt curing process, the\nsalted duck eggs have a briny aroma, a gelatin-like egg white and a\nfirm-textured, round yolk that is bright orange-red in color.</p>\n</blockquote>\n<p><img src=\"./salty_egg.jpg\" alt=\"Chinese Salty Egg\"></p>","frontmatter":{"title":"Test Post","date":"May 01, 2018"}}},"pathContext":{"slug":"/testPost/","previous":{"fields":{"slug":"/hi-folks/"},"frontmatter":{"title":"New Beginnings"}},"next":null}}

